* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
  background-color: grey;
}

button[type='submit'] {
  padding: 5px 15px;
  background: orangered;
  color: aliceblue;
  border: 0 none;
  cursor: pointer;
  border-radius: 5px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.load-more {
  padding: 5px 15px;
  background: orangered;
  color: aliceblue;
  border: 0 none;
  cursor: pointer;
  border-radius: 5px;
}

.is-hidden {
  display: none;
}
